import { useQuery } from '@apollo/client'
import { useQueryParams } from 'common/hooks'
import { DraggableItem, Series, Study } from 'common/types'
import { GET_STUDIES } from './gql'
import PatientDetail from './PatientDetail'

import Hammer from 'hammerjs'
import dicomParser from 'dicom-parser'
import * as cornerstone from 'cornerstone-core'
import * as cornerstoneMath from 'cornerstone-math'
import * as cornerstoneWADOImageLoader from 'cornerstone-wado-image-loader'
import * as cornerstoneTools from 'cornerstone-tools'
import {
  parseImageItem,
  GRIDS,
  DEFAULT_LAYOUT,
  parseStudiesByYear,
  parseInferenceItem,
} from 'common/utils'
import { useEffect, useMemo, useState } from 'react'
import { ItemContext, ItemContextType } from './hooks'

function _initCornerstone() {
  // Externals
  cornerstoneWADOImageLoader.external.cornerstone = cornerstone
  cornerstoneWADOImageLoader.external.dicomParser = dicomParser
  cornerstoneTools.external.cornerstoneMath = cornerstoneMath
  cornerstoneTools.external.cornerstone = cornerstone
  cornerstoneTools.external.Hammer = Hammer
  cornerstoneTools.init({
    showSVGCursors: true,
    mouseEnabled: true,
  })
}

function parseDefaultItems(series: Series): DraggableItem[] {
  const newItems: DraggableItem[] = []
  series.images.forEach((img) => {
    newItems.push(parseImageItem(img, series.series_inst_uid))
  })

  // If current series doesn't fill all the items, fill it with inference result
  if (newItems.length < GRIDS[DEFAULT_LAYOUT]) {
    series.inference.forEach((inf) => {
      newItems.push(parseInferenceItem(inf, series.series_inst_uid))
    })
  }

  // If current series still have empty block, fill it with null
  if (newItems.length < GRIDS[DEFAULT_LAYOUT]) {
    const emptyItems = new Array(GRIDS[DEFAULT_LAYOUT] - newItems.length).fill(
      parseImageItem(null, '')
    )
    newItems.push(...emptyItems)
  }

  return newItems
}

function parseSpecificItems(
  studies: Study[],
  studyIds: string[],
  imageIds: string[]
): DraggableItem[] {
  const newItems: DraggableItem[] = []

  studies.forEach((study) => {
    if (!studyIds.includes(study.study_inst_uid)) {
      return
    }

    study.series.forEach(({ images, series_inst_uid }) => {
      images.forEach((img) => {
        if (!imageIds.includes(img.sop_inst_uid)) {
          return
        }
        newItems.push(parseImageItem(img, series_inst_uid))
      })
    })
  })

  // If specific items are less than DEFAULT_LAYOUT, fill it with null
  if (newItems.length < GRIDS[DEFAULT_LAYOUT]) {
    const emptyItems = new Array(GRIDS[DEFAULT_LAYOUT] - newItems.length).fill(
      parseImageItem(null, '')
    )
    newItems.push(...emptyItems)
  }

  return newItems
}

function flattenAllSeriesItems(studies: Study[]): DraggableItem[] {
  const items: DraggableItem[] = []
  studies.forEach(({ series }) => {
    series.forEach(({ series_inst_uid, images, inference }) => {
      images.forEach((img) => {
        items.push(parseImageItem(img, series_inst_uid))
      })
      inference.forEach((inf) => {
        items.push(parseInferenceItem(inf, series_inst_uid))
      })
    })
  })
  return items
}

_initCornerstone()

export default function PatientDetailContainer() {
  const studyIds = useQueryParams('studyIds')
  const imageIds = useQueryParams('imageIds')
  const { error, data } = useQuery<{ studies: Study[] }, { ids: string[] }>(GET_STUDIES, {
    variables: { ids: studyIds.split(',') },
  })
  const [studiesByYear, setStudiesByYear] = useState<Map<string, Study[]>>(new Map())
  const [defaultItems, setDefaultItems] = useState<DraggableItem[]>([])
  const contextValue = useMemo<ItemContextType>(() => {
    return {
      allItems: data ? flattenAllSeriesItems(data.studies) : [],
    }
  }, [data])

  useEffect(() => {
    if (!data) return
    setStudiesByYear(parseStudiesByYear(data.studies))
    setDefaultItems(
      imageIds === 'all'
        ? parseDefaultItems(data.studies[0].series[0])
        : parseSpecificItems(data.studies, studyIds.split(','), imageIds.split(','))
    )
  }, [data, imageIds, studyIds])

  if (error) {
    return <>'error'</>
  }

  if (studiesByYear.size === 0) {
    return <>'Loading'</>
  }

  return (
    <ItemContext.Provider value={contextValue}>
      <PatientDetail studiesByYear={studiesByYear} defaultItems={defaultItems} />
    </ItemContext.Provider>
  )
}
