import React, { useCallback, useState } from 'react'

import IconText from './components/IconText'
import { ReactComponent as ImportIcon } from 'assets/icon/import.svg'
import { ReactComponent as LayoutIcon } from 'assets/icon/layout.svg'
import { ReactComponent as AiIcon } from 'assets/icon/ai.svg'
import { ReactComponent as ThreeDIcon } from 'assets/icon/3d.svg'
import { ReactComponent as AnalyticsIcon } from 'assets/icon/analytics.svg'
import { ReactComponent as MoreIcon } from 'assets/icon/more.svg'
import { ReactComponent as FleshRemovalIcon } from 'assets/icon/fleshremoval.svg'
import { ReactComponent as Layout1x1Icon } from 'assets/icon/layout_1x1.svg'
import { ReactComponent as Layout1x2Icon } from 'assets/icon/layout_1x2.svg'
import { ReactComponent as Layout2x1Icon } from 'assets/icon/layout_2x1.svg'
import { ReactComponent as Layout2x2Icon } from 'assets/icon/layout_2x2.svg'
import { Box, Menu, MenuItem } from '@material-ui/core'
import DicomTool from './components/DicomTool'
import { Layout } from 'common/utils'
import AiModelTabs from './components/AiModelTabs'
import { DicomToolType, DraggableItem, Plugin, Protocols } from 'common/types'
import { useMenuStyles } from './styles'
import HangingProtocol from './components/HangingProtocol'

const MENU_ITEMS: { layout: Layout; icon: any }[] = [
  { layout: '1x1', icon: <Layout1x1Icon /> },
  { layout: '1x2', icon: <Layout1x2Icon /> },
  { layout: '2x1', icon: <Layout2x1Icon /> },
  { layout: '2x2', icon: <Layout2x2Icon /> },
]

interface Props {
  layout: Layout
  currentItem: DraggableItem
  changeLayout: (newLayout: Layout) => void
  onSwitchPlugin: (plugin: Plugin) => void
  onSwitchDicomTool: (toolType: DicomToolType) => void
  onSwitchHangingProtocol: (protocol: Protocols, items: DraggableItem[]) => void
}

export default function Toolbar(props: Props) {
  const {
    layout,
    currentItem,
    changeLayout,
    onSwitchPlugin,
    onSwitchDicomTool,
    onSwitchHangingProtocol,
  } = props
  const classes = useMenuStyles()
  const [showLayoutMenu, toggleLayoutMenu] = useState(false)
  const [menuAnchor, setMenuAnchor] = useState<null | HTMLElement>(null)

  const onSwitchLayout = useCallback(
    (item: Layout) => {
      changeLayout(item)
      toggleLayoutMenu(false)
    },
    [changeLayout]
  )

  return (
    <section style={{ position: 'relative' }}>
      <Box display="flex" justifyContent="space-between" pl="7.5px" pr="20px">
        {/* Basic dicom functionality */}
        <Box display="flex">
          <DicomTool
            dicomToolType={currentItem.dicomToolType}
            onSwitchDicomTool={onSwitchDicomTool}
          />
          <IconText icon={<ImportIcon />} text="Import" type="primary" disabled={true} />
          <IconText
            icon={<LayoutIcon />}
            text="Layout"
            type="primary"
            onClick={(event: React.MouseEvent<HTMLElement>) => {
              setMenuAnchor(event.currentTarget)
              toggleLayoutMenu(true)
            }}
          />
        </Box>

        <HangingProtocol
          layout={layout}
          currentSeriesId={currentItem.series_inst_uid}
          onSwitchHangingProtocol={onSwitchHangingProtocol}
        />

        {/* Plugin and setting */}
        <Box display="flex" justifyContent="flex-end">
          <IconText
            icon={<AiIcon />}
            text="AI"
            type="plugin"
            active={currentItem.pluginType === 'ai'}
            onClick={() => onSwitchPlugin('ai')}
          />
          <IconText
            icon={<ThreeDIcon />}
            text="3D"
            type="plugin"
            active={currentItem.pluginType === 'ct'}
            onClick={() => onSwitchPlugin('ct')}
          />
          <IconText
            icon={<FleshRemovalIcon />}
            text="Flesh removal"
            type="plugin"
            active={currentItem.pluginType === 'fleshRemoval'}
            onClick={() => onSwitchPlugin('fleshRemoval')}
          />
          <IconText icon={<AnalyticsIcon />} text="Analytics" type="plugin" disabled={true} />
          <IconText icon={<MoreIcon />} text="Settings" type="plugin" disabled={true} />
        </Box>
      </Box>

      {currentItem.pluginType && <AiModelTabs />}

      <Menu
        anchorEl={menuAnchor}
        open={showLayoutMenu}
        classes={{ paper: classes.layoutMenu }}
        onClose={() => toggleLayoutMenu(false)}
      >
        {MENU_ITEMS.map((item) => (
          <MenuItem
            key={item.layout}
            selected={layout === item.layout}
            onClick={() => onSwitchLayout(item.layout)}
            className={classes.menuItem}
          >
            {item.icon}
            <span className="layout-label">{item.layout}</span>
          </MenuItem>
        ))}
      </Menu>
    </section>
  )
}
