import { ReactComponent as PaPaIcon } from 'assets/icon/papa.svg'
import { ReactComponent as PaLatIcon } from 'assets/icon/palat.svg'
import { ReactComponent as HPIcon } from 'assets/icon/hp.svg'
import { Box, Button } from '@material-ui/core'
import IconText from './IconText'
import { useCallback, useEffect, useState } from 'react'
import { client } from 'common/apolloClient'
import { useQueryParams } from 'common/hooks'
import { GET_STUDIES } from 'PatientDetail/gql'
import { DraggableItem, Laterality, Prediction, Protocols, Study } from 'common/types'
import { Layout, parseHangingProtocolItems } from 'common/utils'
import { useHangingProtocolStyles } from 'Toolbar/styles'

function parsePredictionItemsMap(studies: Study[]): Map<string, Prediction[]> {
  const map = new Map()
  studies.forEach(({ series }) => {
    series.forEach(({ series_inst_uid, predictions }) => {
      map.set(series_inst_uid, predictions)
    })
  })
  return map
}

interface Props {
  layout: Layout
  currentSeriesId: string
  onSwitchHangingProtocol: (protocol: Protocols, items: DraggableItem[]) => void
}

export default function HangingProtocol(props: Props) {
  const { layout, currentSeriesId, onSwitchHangingProtocol } = props
  const classes = useHangingProtocolStyles()
  const studyIds = useQueryParams('studyIds')
  const data = client.readQuery<{ studies: Study[] }, { ids: string[] }>({
    query: GET_STUDIES,
    variables: { ids: studyIds.split(',') },
  })
  const [hpItemMap, setHpItemMap] = useState<Map<string, Prediction[]>>(
    parsePredictionItemsMap(data ? data.studies : [])
  )
  const [laterality, setLaterality] = useState<Laterality>('L')

  useEffect(() => {
    if (!data) return

    setHpItemMap(parsePredictionItemsMap(data.studies))
  }, [data])

  const onSwitchHp = useCallback(
    (protocol: Protocols, laterality: Laterality = 'L') => {
      // TODO: user defined protocol
      const items = parseHangingProtocolItems(
        hpItemMap.get(currentSeriesId)!,
        currentSeriesId,
        protocol,
        laterality
      )
      // TODO: error handling
      if (items.length === 0) {
        return
      }
      onSwitchHangingProtocol(protocol, items)
    },
    [currentSeriesId, onSwitchHangingProtocol, hpItemMap]
  )

  const onSwitchLaterality = useCallback(
    (newLaterality: Laterality) => {
      setLaterality(newLaterality)
      onSwitchHp('pa/lat', newLaterality)
    },
    [onSwitchHp]
  )

  return (
    <>
      {layout === 'pa/lat' && (
        <div className={classes.root}>
          <Button
            variant="text"
            className={laterality === 'L' ? 'active' : ''}
            onClick={() => onSwitchLaterality('L')}
          >
            Left
          </Button>
          <Button
            variant="text"
            className={laterality === 'R' ? 'active' : ''}
            onClick={() => onSwitchLaterality('R')}
          >
            Right
          </Button>
        </div>
      )}
      <Box display="flex" justifyContent="space-between">
        <IconText
          icon={<PaPaIcon />}
          text="PA/PA"
          type="secondary"
          active={layout === 'pa/pa'}
          onClick={() => onSwitchHp('pa/pa')}
        />
        <IconText
          icon={<PaLatIcon />}
          text="PA/LAT"
          type="secondary"
          active={layout === 'pa/lat'}
          onClick={() => onSwitchHp('pa/lat')}
        />
        <IconText
          icon={<HPIcon />}
          text="User HP"
          type="secondary"
          active={false}
          disabled={true}
          onClick={() => onSwitchHp('user-hp')}
        />
      </Box>
    </>
  )
}
