import { Box, Dialog, Grid } from '@material-ui/core'
import { OAResult } from 'common/types'
import { FILES_URL } from 'common/utils'
import { ItemContext } from 'PatientDetail/hooks'
import { useSummaryStyles } from 'PatientDetail/styles'
import { useCallback, useContext, useMemo, useRef, useState } from 'react'

function gradeColor(grade: number) {
  if (grade < 1) {
    return '#FFFFFF'
  } else if (grade < 2) {
    return '#FFB800'
  }
  return '#FF0000'
}

// imgPath: "R.js.filepath"
// We need to return data.R.js.filepath
function parseImageFilepath(data: OAResult, imgPath: string) {
  let filepath = data
  imgPath.split('.').forEach((path) => {
    filepath = (filepath as any)[path]
  })

  return filepath
}

const Detected: React.FC<{ detected: boolean }> = ({ detected }) => (
  <span className={detected ? 'highlight' : ''}>{detected ? 'Detected' : 'No'}</span>
)

const Image: React.FC<{
  imgPath: string
  alt: string
  data: OAResult
  onClick: (img: string) => void
}> = ({ imgPath, alt, data, onClick }) => {
  return (
    <img
      style={{ cursor: 'pointer' }}
      src={`${FILES_URL}/${parseImageFilepath(data, imgPath)}`}
      alt={alt}
      onClick={() => onClick(imgPath)}
    />
  )
}

const CustomGrid: React.FC<{ className: string; rightKnee: any; label: any; leftKnee: any }> = ({
  className,
  rightKnee,
  label,
  leftKnee,
}) => (
  <Grid container={true} alignItems="center" className={className}>
    <Grid className="left" item={true} xs container={true} justify="flex-end">
      {rightKnee}
    </Grid>
    <Grid className="label" item={true} xs={3} container={true} justify="center">
      {label}
    </Grid>
    <Grid className="right" item={true} xs container={true} justify="space-between">
      {leftKnee}
    </Grid>
  </Grid>
)

const HistoryDots: React.FC<{
  oaHistories: { id: string; data: OAResult }[]
  currentResultIdx: number
  onSwitchHistory: (idx: number) => void
  className: string
}> = (props) => {
  return (
    <div className={props.className}>
      {props.oaHistories.map((_, idx) => {
        const isActive = props.currentResultIdx === idx
        return (
          <span
            key={idx}
            className={`dot ${isActive ? 'active' : ''}`}
            onClick={() => props.onSwitchHistory(idx)}
          ></span>
        )
      })}
    </div>
  )
}

interface Props {
  defaultId: string
  withThumbnail: boolean
}

export default function OASummary(props: Props) {
  const { defaultId, withThumbnail } = props
  const classes = useSummaryStyles({ withThumbnail })
  const [currentImgPath, setCurrentImgPath] = useState<string | null>(null)

  const { allItems } = useContext(ItemContext)
  const oaHistories = useMemo<{ id: string; data: OAResult }[]>(
    () =>
      allItems
        .filter(({ func }) => func === 'oa')
        .map((item) => ({ id: item.id, data: item.data as OAResult })),
    [allItems]
  )
  const [currentResultIdx, setResultIdx] = useState<number>(
    oaHistories.findIndex(({ id }) => id === defaultId)
  )
  const previousResultIdx = useRef<number>(0)

  const onOpenImageDialog = useCallback(
    (imgPath: string) => {
      previousResultIdx.current = currentResultIdx
      setCurrentImgPath(imgPath)
    },
    [currentResultIdx]
  )

  const customGridClasses = `${classes.customGrid} ${withThumbnail ? '' : 'small'}`
  const { L, R } = oaHistories[currentResultIdx].data

  return (
    <div className={classes.summary} style={{ height: withThumbnail ? '90%' : '100%' }}>
      <div className={classes.title}>Knee Osteoarthritis AI Labeling Summary</div>

      <HistoryDots
        className={classes.historyDots}
        oaHistories={oaHistories}
        currentResultIdx={currentResultIdx}
        onSwitchHistory={setResultIdx}
      />

      <section className={classes.section}>
        <CustomGrid
          className={`${customGridClasses} ${classes.direction}`}
          rightKnee="Right"
          label=""
          leftKnee="Left"
        />

        {/* KL Grade */}
        <CustomGrid
          className={`${customGridClasses} ${classes.klGrade}`}
          rightKnee={
            <span
              style={{
                fontSize: '30px',
                lineHeight: '40.8px',
                color: gradeColor(R.klGrade.KL_GRADE),
              }}
            >
              {R.klGrade.KL_GRADE}
            </span>
          }
          label={<span style={{ fontSize: '24px', lineHeight: '30.7px' }}>KL Grade</span>}
          leftKnee={
            <span
              style={{
                fontSize: '30px',
                lineHeight: '40.8px',
                color: gradeColor(L.klGrade.KL_GRADE),
              }}
            >
              {L.klGrade.KL_GRADE}
            </span>
          }
        />

        {/* Joint space width */}
        <div
          style={{
            fontSize: '20px',
            lineHeight: '27px',
            marginBottom: '30px',
            textAlign: 'center',
          }}
        >
          <span
            style={{
              fontSize: '30px',
              lineHeight: '40.8px',
              color: gradeColor(Math.max(R.js.medJSN, R.js.latJSN)),
            }}
          >
            {Math.max(R.js.medJSN, R.js.latJSN)}
          </span>
          <span
            style={{
              display: 'inline-block',
              minWidth: '25%',
              margin: '0px 6%',
            }}
          >
            Joint Space Narrowing
          </span>
          <span
            style={{
              fontSize: '30px',
              lineHeight: '40.8px',
              color: gradeColor(Math.max(L.js.medJSN, L.js.latJSN)),
            }}
          >
            {Math.max(L.js.medJSN, L.js.latJSN)}
          </span>
        </div>
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" flex="1">
              {withThumbnail && (
                <Image
                  imgPath="R.js.filepath"
                  alt="JSN"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
              <Box display="flex" justifyContent="space-between" flexDirection="column">
                <div>{R.js.mJSW}mm</div>
                <div>{R.js.std_mJSW}mm</div>
              </Box>
            </Box>
          }
          label={
            <Box display="flex" justifyContent="space-between" flexDirection="column" height="100%">
              <div className="gray">Joint Space Width</div>
              <div className="gray">Standardized Joint Space Width</div>
            </Box>
          }
          leftKnee={
            <Box display="flex" justifyContent="space-between" flex="1">
              <Box display="flex" justifyContent="space-between" flexDirection="column">
                <div>{L.js.mJSW}mm</div>
                <div>{L.js.std_mJSW}mm</div>
              </Box>
              {withThumbnail && (
                <Image
                  imgPath="L.js.filepath"
                  alt="JSN"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
            </Box>
          }
        />

        {/* Osteophyte */}
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              {withThumbnail && (
                <Image
                  imgPath="R.osteophyte.filepath"
                  alt="OP"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
              <Detected detected={R.osteophyte.detected} />
            </Box>
          }
          label="Osteophyte"
          leftKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              <Detected detected={L.osteophyte.detected} />
              {withThumbnail && (
                <Image
                  imgPath="L.osteophyte.filepath"
                  alt="OP"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
            </Box>
          }
        />

        {/* Sclerosis */}
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              {withThumbnail && (
                <Image
                  imgPath="R.sclerosis.filepath"
                  alt="SC"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
              <Detected detected={R.sclerosis.detected} />
            </Box>
          }
          label="Sclerosis"
          leftKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              <Detected detected={L.sclerosis.detected} />
              {withThumbnail && (
                <Image
                  imgPath="R.sclerosis.filepath"
                  alt="SC"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
            </Box>
          }
        />

        {/* Alignment */}
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              {withThumbnail && (
                <Image
                  imgPath="R.alignment.filepath"
                  alt="Alignment"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
              <span>{R.alignment.FTA} </span>
            </Box>
          }
          label="Alignment"
          leftKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              <span>{L.alignment.FTA} </span>
              {withThumbnail && (
                <Image
                  imgPath="L.alignment.filepath"
                  alt="Alignment"
                  data={oaHistories[currentResultIdx].data}
                  onClick={onOpenImageDialog}
                />
              )}
            </Box>
          }
        />
      </section>

      {currentImgPath && (
        <Dialog
          open={true}
          onClose={() => {
            // Switch back to the original result because we should not
            // change the oa result page after the dialog is closed
            setResultIdx(previousResultIdx.current)
            setCurrentImgPath(null)
          }}
          maxWidth="md"
          classes={{ paper: classes.dialog }}
        >
          <HistoryDots
            className={classes.historyDots}
            oaHistories={oaHistories}
            currentResultIdx={currentResultIdx}
            onSwitchHistory={setResultIdx}
          />
          <img
            src={`${FILES_URL}/${parseImageFilepath(
              oaHistories[currentResultIdx].data,
              currentImgPath
            )}`}
            alt="img"
          />
        </Dialog>
      )}
    </div>
  )
}
