import { Theme } from '@material-ui/core'
import { makeStylesWithIndex } from 'common/hooks'

type IconType = 'primary' | 'secondary' | 'plugin'

interface Props {
  icon: React.ReactElement
  text: string
  onClick?: (event: any) => void
  type: IconType
  active?: boolean
  disabled?: boolean
}

const TYPE_COLOR_MAP: Record<IconType, string> = {
  primary: '#71C2D9',
  secondary: 'rgba(255, 255, 255, 0.7)',
  plugin: '#F26A65',
}

const TYPE_BACKGROUND_MAP: Record<IconType, string> = {
  primary: 'rgba(116, 194, 216, 0.7)',
  secondary: 'rgba(116, 194, 216, 0.7)',
  plugin: '#F26A65',
}

const useStyles = makeStylesWithIndex<
  Theme,
  { type: IconType; active?: boolean; disabled?: boolean }
>({
  iconText: {
    pointerEvents: (props) => (props.disabled ? 'none' : 'unset'),
    opacity: (props) => (props.disabled ? 0.5 : 1),
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    height: '73px',
    width: '57px',
    boxSizing: 'border-box',
    minWidth: '50px',
    textAlign: 'center',
    background: (props) => (props.active ? TYPE_BACKGROUND_MAP[props.type] : 'inherit'),
    color: (props) => (props.active ? '#FFFFFF' : TYPE_COLOR_MAP[props.type]),
    fill: (props) => (props.active ? '#FFFFFF' : TYPE_COLOR_MAP[props.type]),
    '&:hover': {
      background: (props) => TYPE_BACKGROUND_MAP[props.type],
      color: '#FFFFFF',
      cursor: 'pointer',
      fill: '#FFFFFF',
    },
  },
  svg: {
    marginBottom: '5px',
  },
  text: {
    fontSize: '12px',
    lineHeight: '15px',
    height: '30px',
    transform: 'scale(0.916)', // 12px -> 11px
    whiteSpace: 'pre-line',
  },
})

export default function IconText({ icon, text, onClick, type, active, disabled }: Props) {
  const classes = useStyles({ type, active, disabled })

  return (
    <div className={classes.iconText} onClick={onClick}>
      <span className={classes.svg}>{icon}</span>
      <div className={classes.text}>{text}</div>
    </div>
  )
}
