import { Grid, GridSize } from '@material-ui/core'
import { useDrop } from 'react-dnd'
import ImageViewer from './ImageViewer'
import ThreeDViewer from './ThreeDViewer'
import History from './History'
import { useCallback, useEffect } from 'react'
import { GRIDS, Layout } from 'common/utils'
import OASummary from './OASummary'
import { DraggableItem } from 'common/types'
import { useLayoutPanelStyles } from 'PatientDetail/styles'
import { ReactComponent as BackspaceIcon } from 'assets/icon/backspace.svg'

const GRID_SIZE: Record<Layout, GridSize> = {
  '1x1': 12,
  '1x2': 6,
  '2x1': 12,
  '2x2': 6,
  'pa/pa': 6,
  'pa/lat': 6,
}

const LayoutItem: React.FC<{
  dragIdx: number
  itemId: string
  children: React.ReactElement
  isFocused: boolean
  onDropItem: (item: DraggableItem, targetIdx: number) => void
}> = (props) => {
  const classes = useLayoutPanelStyles()
  const [{ isOver, canDrop }, drop] = useDrop(
    () => ({
      accept: 'box',
      drop: (item: DraggableItem) => {
        props.onDropItem(item, props.dragIdx)
        // return previous item id to notify which item is deactivated
        return { previousItemId: props.itemId }
      },
      collect: (monitor: any) => ({
        isOver: monitor.isOver(),
        canDrop: monitor.canDrop(),
      }),
    }),
    []
  )

  const opacity = isOver && canDrop ? 0.4 : 1
  const border = canDrop ? '1px solid #6EC4D0' : 'unset'

  return (
    <div ref={drop} className={classes.item} style={{ opacity, border }}>
      {props.children}
      {
        // Element without focus should be overlaid by a transparent div to block mouse event
        !props.isFocused && <div className={classes.overlap} />
      }
    </div>
  )
}

interface Props {
  layout: Layout
  items: DraggableItem[]
  focusedIdx: number
  changeLayout: (layout: Layout) => void
  backFromFullscreen: () => void
  focusItem: (targetIdx: number) => void
  switchTargetItem: (item: DraggableItem, targetIdx: number) => void
}

export default function LayoutPanel(props: Props) {
  const {
    layout,
    items,
    focusedIdx,
    changeLayout,
    backFromFullscreen,
    focusItem,
    switchTargetItem,
  } = props
  const classes = useLayoutPanelStyles()

  useEffect(() => {
    // Trigger a resize event to re-render 2D & 3D viewer
    window.dispatchEvent(new Event('resize'))
  }, [layout])

  const onDoubleClickItem = useCallback(
    (idx: number) => {
      focusItem(idx)
      if (layout === '1x1') {
        backFromFullscreen()
      } else {
        changeLayout('1x1')
      }
    },
    [focusItem, changeLayout, backFromFullscreen, layout]
  )

  function renderContent(item: DraggableItem, isFocused: boolean) {
    if (!item) {
      return <>N/A</>
    }
    switch (item.func) {
      case 'dicom':
        const pluginType = item.pluginType
        const enableFleshRemoval = pluginType === 'fleshRemoval'
        const enableCT = pluginType === 'ct'
        return (
          <ImageViewer
            id={`xray-${item.id}`}
            filepath={item.file}
            plugin={item.plugin}
            isFocused={isFocused}
            enableFleshRemoval={enableFleshRemoval}
            enableCT={enableCT}
          />
        )
      case 'hanging-protocol':
        return (
          <ImageViewer
            id={`hanging-protocol-${item.id}`}
            filepath={item.file}
            plugin={item.plugin}
            isFocused={isFocused}
            enableFleshRemoval={false}
            enableCT={false}
          />
        )
      case '3d':
        return <ThreeDViewer id={`3d-${item.id}`} filepath={item.file} />
      case 'history':
        return <History />
      case 'oa':
        const withThumbnail = layout === '1x1' || layout === '2x1'
        return <OASummary defaultId={item.id} withThumbnail={withThumbnail} />
      default:
        return <>N/A</>
    }
  }

  const gridHeight = layout === '2x1' || layout === '2x2' ? '50%' : '100%'
  const isFullscreenMode = layout === '1x1'
  return (
    <Grid container className={classes.layoutPanel}>
      {layout === '1x1' && (
        <BackspaceIcon className={classes.backspace} onClick={backFromFullscreen} />
      )}
      {items.map((item, idx) => {
        const isFocused = focusedIdx === idx
        const isVisible =
          (idx < GRIDS[layout] && !isFullscreenMode) || (isFullscreenMode && isFocused)
        const key = `${item?.id}-${idx}`
        if (isVisible) {
          return (
            <Grid
              key={key} //use func+id+idx as key to avoid re-render when the layout is changed
              item={true}
              xs={GRID_SIZE[layout]}
              style={{ height: gridHeight }}
              onClick={() => focusItem(idx)}
              onDoubleClick={() => onDoubleClickItem(idx)}
            >
              <LayoutItem
                dragIdx={idx}
                itemId={item?.id}
                isFocused={isFocused}
                onDropItem={switchTargetItem}
              >
                {renderContent(item, isFocused)}
              </LayoutItem>
            </Grid>
          )
        }

        // All the invisible items "should not" be unmounted.
        // Alternatively, we put it out of screen
        return (
          <Grid
            key={key}
            item={true}
            xs={GRID_SIZE[layout]}
            style={{ position: 'absolute', top: '0px', right: '100%' }}
          >
            <LayoutItem
              dragIdx={idx}
              itemId={key}
              isFocused={isFocused}
              onDropItem={switchTargetItem}
            >
              {renderContent(item, isFocused)}
            </LayoutItem>
          </Grid>
        )
      })}
    </Grid>
  )
}
