import { ReactComponent as BrightnessIcon } from 'assets/icon/brightness.svg'
import { ReactComponent as PanIcon } from 'assets/icon/pan.svg'
import { ReactComponent as MeasurementIcon } from 'assets/icon/measurement.svg'
import { ReactComponent as MagnifierIcon } from 'assets/icon/magnifier.svg'
import IconText from './IconText'
import React, { useEffect, useRef, useState } from 'react'
import { globalSubject, SubjectTopic } from 'common/utils'
import * as cornerstoneTools from 'cornerstone-tools'
import { DicomToolType, MeasurementTool } from 'common/types'
import { KeyCode, shortcut$ } from 'common/shortcut'
import { Menu, MenuItem } from '@material-ui/core'
import { useMenuStyles } from 'Toolbar/styles'

enum MouseClick {
  Left = 1,
  Right = 2,
  Middle = 4,
}

const MENU_ITEMS: { tool: MeasurementTool; icon: any }[] = [
  { tool: 'Length', icon: <MeasurementIcon /> },
  { tool: 'Angle', icon: <MeasurementIcon /> },
]

interface Props {
  dicomToolType: DicomToolType | null
  onSwitchDicomTool: (toolType: DicomToolType) => void
}

function _initCornerstoneTool() {
  cornerstoneTools.addTool(
    cornerstoneTools.ZoomMouseWheelTool, // Optional configuration
    {
      configuration: {
        invert: false,
        preventZoomOutsideImage: false,
        minScale: 0.1,
        maxScale: 2,
      },
    }
  )
  // addTool: initialize, setToolPassive: being able to view the annotations
  cornerstoneTools.addTool(cornerstoneTools.PanTool)
  cornerstoneTools.setToolPassive('Pan')
  cornerstoneTools.addTool(cornerstoneTools.WwwcTool)
  cornerstoneTools.setToolPassive('Wwwc')
  cornerstoneTools.addTool(cornerstoneTools.MagnifyTool)
  cornerstoneTools.setToolPassive('Magnify')
  cornerstoneTools.addTool(cornerstoneTools.AngleTool)
  cornerstoneTools.setToolPassive('Angle')
  cornerstoneTools.addTool(cornerstoneTools.LengthTool)
  cornerstoneTools.setToolPassive('Length')
}

export default function DicomTool(props: Props) {
  const { dicomToolType, onSwitchDicomTool } = props
  const classes = useMenuStyles()
  const prevDicomTool = useRef<DicomToolType | null>(dicomToolType)
  const [showLayoutMenu, toggleLayoutMenu] = useState(false)
  const [menuAnchor, setMenuAnchor] = useState<null | HTMLElement>(null)

  useEffect(() => {
    const subscription = globalSubject.subscribe(SubjectTopic.InitXrayTool, () => {
      _initCornerstoneTool()
    })
    return () => subscription.unsubscribe()
  }, [])

  useEffect(() => {
    const brightness = shortcut$([KeyCode.ControlLeft, KeyCode.KeyB])
      .pipe()
      .subscribe(() => {
        onSwitchDicomTool('Wwwc')
      })
    const panZoom = shortcut$([KeyCode.ControlLeft, KeyCode.KeyZ])
      .pipe()
      .subscribe(() => {
        onSwitchDicomTool('PanZoom')
      })

    const length = shortcut$([KeyCode.ControlLeft, KeyCode.KeyD])
      .pipe()
      .subscribe(() => {
        onSwitchDicomTool('Length')
      })

    const angle = shortcut$([KeyCode.ControlLeft, KeyCode.KeyG])
      .pipe()
      .subscribe(() => {
        onSwitchDicomTool('Angle')
      })

    const magnify = shortcut$([KeyCode.ControlLeft, KeyCode.KeyM])
      .pipe()
      .subscribe(() => {
        onSwitchDicomTool('Magnify')
      })

    return () => {
      brightness.unsubscribe()
      panZoom.unsubscribe()
      length.unsubscribe()
      angle.unsubscribe()
      magnify.unsubscribe()
    }
  }, [onSwitchDicomTool])

  useEffect(() => {
    // Disable previous tool
    switch (prevDicomTool.current) {
      case 'PanZoom':
        cornerstoneTools.setToolPassive('Pan')
        cornerstoneTools.setToolPassive('ZoomMouseWheel')
        break
      case null:
        break
      default:
        cornerstoneTools.setToolPassive(prevDicomTool.current)
    }
    // Activate new tool
    switch (dicomToolType) {
      case 'PanZoom':
        cornerstoneTools.setToolActive('Pan', { mouseButtonMask: MouseClick.Left })
        cornerstoneTools.setToolActive('ZoomMouseWheel', { mouseButtonMask: MouseClick.Left })
        break
      case null:
        break
      default:
        cornerstoneTools.setToolActive(dicomToolType as cornerstoneTools.ToolName, {
          mouseButtonMask: MouseClick.Left,
        })
    }
    prevDicomTool.current = dicomToolType
  }, [dicomToolType])

  return (
    <>
      <IconText
        icon={<BrightnessIcon />}
        text={`Brightness & Contrast`}
        type="primary"
        active={dicomToolType === 'Wwwc'}
        onClick={() => onSwitchDicomTool('Wwwc')}
      />
      <IconText
        icon={<PanIcon />}
        text="Pan & Zoom"
        type="primary"
        active={dicomToolType === 'PanZoom'}
        onClick={() => onSwitchDicomTool('PanZoom')}
      />
      <IconText
        icon={<MeasurementIcon />}
        text="Measu-rement"
        type="primary"
        active={dicomToolType === 'Length' || dicomToolType === 'Angle'}
        // onClick={() => onSwitchDicomTool('Measurement')}
        onClick={(event: React.MouseEvent<HTMLElement>) => {
          setMenuAnchor(event.currentTarget)
          toggleLayoutMenu(true)
        }}
      />
      <IconText
        icon={<MagnifierIcon />}
        text="Magnifier"
        type="primary"
        active={dicomToolType === 'Magnify'}
        onClick={() => onSwitchDicomTool('Magnify')}
      />
      <Menu
        anchorEl={menuAnchor}
        open={showLayoutMenu}
        classes={{ paper: classes.layoutMenu }}
        onClose={() => toggleLayoutMenu(false)}
      >
        {MENU_ITEMS.map((item) => {
          const isSelected = dicomToolType === item.tool
          return (
            <MenuItem
              key={item.tool}
              selected={isSelected}
              onClick={() => {
                onSwitchDicomTool(item.tool)
                toggleLayoutMenu(false)
              }}
              className={`${classes.menuItem} ${isSelected ? 'selected' : ''}`}
            >
              {item.icon}
              <span className="layout-label">{item.tool}</span>
            </MenuItem>
          )
        })}
      </Menu>
    </>
  )
}
