import { Box } from '@material-ui/core'
import { ChartData, ChartDataSets, ChartOptions } from 'chart.js'
import { makeStylesWithIndex } from 'common/hooks'
import { Line } from 'react-chartjs-2'

const useStyles = makeStylesWithIndex({
  history: {
    background: 'rgba(59, 124, 156, 0.3)',
    height: '100%',
    overflowY: 'scroll',
    '&::-webkit-scrollbar': {
      display: 'none',
    },
  },
  title: {
    color: '#6EC4D0',
    background: 'rgba(46, 46, 46, 0.7)',
    textAlign: 'center',
    fontWeight: 'bold',
    fontSize: '14px',
    lineHeight: '19px',
    padding: '9px 0px',
  },
  parts: {
    color: 'rgba(255, 255, 255, 0.7)',
    fontWeight: 500,
    borderBottom: '0.5px solid #6EC4D0',
    margin: '16px 19px 6px',
    paddingBottom: '6px',
    '& > div': {
      width: '50%',
    },
    '& .left': {
      textAlign: 'end',
      marginRight: '40px',
    },
  },
  chartSection: {
    padding: '10px 0px',
    margin: '0px 19px',
    borderBottom: '0.5px solid rgba(110, 196, 208, 0.3)',
    '& .chart-name': {
      fontSize: '14px',
      lineHeight: '19px',
      color: '#70C4D7',
      margin: '0px 0px 4px',
      fontWeight: 500,
    },
    '& .chart-sub-name': {
      fontSize: '12px',
      lineHeight: '16px',
      fontWeight: 400,
      marginBottom: '2px',
      color: 'rgba(255, 255, 255, 0.7)',
      '&:not(:first-of-type)': {
        marginTop: '5px',
      },
    },
    '& .chart-bg': {
      background: '#2E2E2E',
      padding: '3px 5px',
      boxSizing: 'border-box',
      width: '50%',
      '&.first': {
        marginRight: '2px',
      },
    },
  },
})

const data: ChartData = {
  labels: [
    '2021-03-01T08:00:00Z',
    '2021-03-03T08:00:00Z',
    '2021-03-05T09:00:00Z',
    '2021-03-09T08:00:00Z',
    '2021-03-12T07:00:00Z',
    '2021-04-08T08:00:00Z',
    '2021-04-16T08:00:00Z',
    '2021-06-12T07:00:00Z',
    '2021-08-08T08:00:00Z',
  ],
  datasets: [
    {
      data: [0.5, 0.7, 1, 1.3, 1.5, 2, 1.7, 2.7, 1.6],
      fill: false,
      lineTension: 0,
      pointRadius: 2,
      hitRadius: 3,
      showLine: false,
      backgroundColor: (context: { dataIndex?: number; dataset?: ChartDataSets }) => {
        var index = context.dataIndex!
        var value = context.dataset?.data![index]!
        if (value < 1) {
          return '#24FF00'
        } else if (value < 2) {
          return '#FFB800'
        }
        return '#FF0000'
      },
    },
  ],
}

const options: ChartOptions = {
  scales: {
    yAxes: [
      {
        ticks: {
          beginAtZero: true,
          fontSize: 6,
          fontFamily: 'Noto Sans, sans-serif',
          fontColor: '#FFFFFF',
        },
      },
    ],
    xAxes: [
      {
        ticks: {
          autoSkip: true,
          callback: (value) => {
            const date = new Date(value)
            return `${date.getMonth() + 1}/${date.getDate()} ${date.getFullYear() - 1911}`
          },
          fontSize: 6,
          fontColor: '#FFFFFF',
          fontFamily: 'Noto Sans, sans-serif',
        },
      },
    ],
  },
  legend: {
    display: false,
  },
  tooltips: {
    xPadding: 9,
    yPadding: 5,
    callbacks: {
      title: (tooltipItem) => {
        const date = new Date(tooltipItem[0].xLabel!)
        return `${date.getFullYear() - 1911}/${date.getMonth() + 1}/${date.getDay()}`
      },
      labelTextColor: (tooltipItem) => {
        const value = parseFloat(tooltipItem.value!)
        if (value < 1) {
          return '#24FF00'
        } else if (value < 2) {
          return '#FFB800'
        }
        return '#FF0000'
      },
    },
    titleFontColor: 'rgba(255, 255, 255, 0.7)',
    titleFontSize: 12,
    titleFontFamily: 'Noto Sans, sans-serif',
    bodyFontStyle: 'bold',
    bodyFontSize: 14,
    backgroundColor: 'rgba(59, 123, 156, 0.8)',
    displayColors: false,
    borderColor: 'rgba(112, 196, 215, 0.5)',
    borderWidth: 0.5,
  },
}

export default function History() {
  const classes = useStyles()
  return (
    <div className={classes.history}>
      <div className={classes.title}>Knee Osteoarthritis History</div>

      <Box display="flex" className={classes.parts}>
        <div className="left">Left</div>
        <div>Right</div>
      </Box>

      <section className={classes.chartSection}>
        <h6 className="chart-name">KL Grade</h6>
        <Box display="flex">
          <div className="chart-bg first">
            <Line data={data} options={options}></Line>
          </div>
          <div className="chart-bg">
            <Line data={data} options={options}></Line>
          </div>
        </Box>
      </section>

      <section className={classes.chartSection}>
        <h6 className="chart-name">Joint space width</h6>
        <div className="chart-sub-name">Standard</div>
        <Box display="flex">
          <div className="chart-bg first">
            <Line data={data} options={options}></Line>
          </div>
          <div className="chart-bg">
            <Line data={data} options={options}></Line>
          </div>
        </Box>

        <div className="chart-sub-name">Normalized</div>
        <Box display="flex">
          <div className="chart-bg first">
            <Line data={data} options={options}></Line>
          </div>
          <div className="chart-bg">
            <Line data={data} options={options}></Line>
          </div>
        </Box>
      </section>
    </div>
  )
}
