import { ReactComponent as KneelIcon } from 'assets/icon/knee.svg'
import { ReactComponent as HipIcon } from 'assets/icon/hip.svg'
import { ReactComponent as SpineIcon } from 'assets/icon/spine.svg'
import { ReactComponent as ShoulderIcon } from 'assets/icon/shoulder.svg'
import SummaryImg from 'assets/img/summary.png'
import HistoryImg from 'assets/img/history.png'
import ThreeDLeftImg from 'assets/img/3d-left.png'
import ThreeDRightImg from 'assets/img/3d-right.png'
import React, { useCallback, useState } from 'react'
import { globalSubject, parseInferenceItem, SubjectTopic } from 'common/utils'
import { ModelFunc, Study, ThreeDResult } from 'common/types'
import { useQueryParams } from 'common/hooks'
import { GET_STUDIES } from 'PatientDetail/gql'
import { client } from 'common/apolloClient'
import { useAiModelTabsStyles } from 'Toolbar/styles'

const Tab: React.FC<{
  className: string
  icon: React.ReactElement
  text: string
  active: boolean
  onClick: () => void
}> = ({ className, icon, text, active, onClick }) => {
  return (
    <div
      className={className}
      onClick={onClick}
      style={{
        color: active ? '#FFFFFF' : 'rgba(255, 255, 255, 0.7)',
        borderBottom: active ? 'solid 4px #C53652' : 'none',
      }}
    >
      {icon}
      {text}
    </div>
  )
}

const ImgText: React.FC<{
  className: string
  img: string
  text: string
  active: boolean
  onClick: () => void
}> = ({ className, img, text, active, onClick }) => {
  return (
    <div
      className={className}
      style={{
        border: active ? '1px solid #C53652' : 'inherit',
        borderRadius: '3px',
      }}
      onClick={onClick}
    >
      <img src={img} alt={text} />
      <span style={{ transform: 'scale(0.83)' }}>{text}</span>
    </div>
  )
}

export default function AiModelTabs() {
  const classes = useAiModelTabsStyles()
  const [category, setCategory] = useState('knee')
  const [func, setFunc] = useState<ModelFunc | null>(null)
  const [laterality, setLaterality] = useState<'left' | 'right' | undefined>('left')
  const studyIds = useQueryParams('studyIds').split(',')
  const data = client.readQuery<{ studies: Study[] }, { ids: string[] }>({
    query: GET_STUDIES,
    variables: { ids: studyIds },
  })

  const onSwitchFunc = useCallback(
    (newFunc: ModelFunc, newLaterality?: 'left' | 'right') => {
      // Find the first matched inference result
      data?.studies.forEach((study) => {
        study.series.forEach(({ series_inst_uid, inference }) => {
          for (const inf of inference) {
            if (inf.func !== newFunc) continue
            // 3d model needs to match laterality
            if (newFunc === '3d' && (inf.result as ThreeDResult).laterality !== newLaterality)
              continue

            globalSubject.next(SubjectTopic.SwitchFunc, {
              func: newFunc,
              item: parseInferenceItem(inf, series_inst_uid),
            })
            break
          }
        })
      })
      setLaterality(newLaterality)
      setFunc(newFunc)
    },
    [data]
  )

  return (
    <div className={classes.root}>
      <section className={classes.tabs}>
        <Tab
          className={classes.tab}
          icon={<KneelIcon />}
          text="Knee"
          active={category === 'knee'}
          onClick={() => setCategory('knee')}
        />
        <Tab
          className={classes.tab}
          icon={<HipIcon />}
          text="Pelvis & Hip"
          active={category === 'hip'}
          onClick={() => setCategory('hip')}
        />
        <Tab
          className={classes.tab}
          icon={<SpineIcon />}
          text="Spine"
          active={category === 'spine'}
          onClick={() => setCategory('spine')}
        />
        <Tab
          className={classes.tab}
          icon={<ShoulderIcon />}
          text="Shoulder"
          active={category === 'shoulder'}
          onClick={() => setCategory('shoulder')}
        />
      </section>
      <section className={classes.funcTabs}>
        <ImgText
          className={classes.funcTab}
          img={SummaryImg}
          text="OA Label"
          active={func === 'oa'}
          onClick={() => onSwitchFunc('oa')}
        />
        <ImgText
          className={classes.funcTab}
          img={HistoryImg}
          text="History"
          active={func === 'history'}
          onClick={() => onSwitchFunc('history')}
        />
        <ImgText
          className={classes.funcTab}
          img={ThreeDRightImg}
          text="AR Right"
          active={func === '3d' && laterality === 'right'}
          onClick={() => onSwitchFunc('3d', 'right')}
        />
        <ImgText
          className={classes.funcTab}
          img={ThreeDLeftImg}
          text="AR Left"
          active={func === '3d' && laterality === 'left'}
          onClick={() => onSwitchFunc('3d', 'left')}
        />
      </section>
    </div>
  )
}
