import { Grid } from '@material-ui/core'
import StudyList from './components/StudyList'

import LayoutPanel from './components/LayoutPanel'
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { useCallback, useContext, useEffect, useRef, useState } from 'react'
import Toolbar from 'Toolbar/Toolbar'
import { DEFAULT_LAYOUT, globalSubject, Layout, SubjectTopic } from 'common/utils'
import { Study, Plugin, DraggableItem, DicomToolType, Protocols } from 'common/types'

import { usePatientDetailStyles } from './styles'
import { interval } from 'rxjs'
import { throttle } from 'rxjs/operators'
import { shortcut$, KeyCode } from 'common/shortcut'
import { ItemContext } from './hooks'

interface Props {
  studiesByYear: Map<string, Study[]>
  defaultItems: DraggableItem[]
}

export default function PatientDetail(props: Props) {
  const { studiesByYear, defaultItems } = props
  const classes = usePatientDetailStyles()
  const [layout, setLayout] = useState<Layout>(DEFAULT_LAYOUT)
  const [activeItems, setActiveItems] = useState<DraggableItem[]>(defaultItems)
  const [focusedIdx, setFocusedIdx] = useState(0)
  const prevLayout = useRef<Layout | null>(null)
  const { allItems } = useContext(ItemContext)

  const onSwitchPlugin = useCallback(
    (pluginType: Plugin | null) => {
      switch (pluginType) {
        case 'fleshRemoval':
          if (activeItems[focusedIdx].func !== 'dicom') return
          break
      }

      // To switch plugin or close
      activeItems[focusedIdx] = {
        ...activeItems[focusedIdx],
        pluginType: activeItems[focusedIdx].pluginType === pluginType ? null : pluginType,
      }
      setActiveItems([...activeItems])
    },
    [activeItems, focusedIdx]
  )

  const onSwitchDicomTool = useCallback(
    (toolType: DicomToolType) => {
      setActiveItems((prev) => {
        prev[focusedIdx].dicomToolType = toolType
        return [...prev]
      })
    },
    [focusedIdx]
  )

  const onSwitchHangingProtocol = useCallback((protocol: Protocols, items: DraggableItem[]) => {
    let layout: Layout
    switch (protocol) {
      case 'pa/pa':
        layout = 'pa/pa'
        break
      case 'pa/lat':
        layout = 'pa/lat'
        break
      default:
        // TODO: user defined hp
        return
    }
    setLayout(layout)
    setActiveItems((prev) => {
      items.forEach((item, idx) => (prev[idx] = item))
      return [...prev]
    })
  }, [])

  const switchTargetItem = useCallback((item: DraggableItem, targetIdx: number) => {
    setActiveItems((prev) => {
      const newItems = [...prev]
      newItems[targetIdx] = item
      return newItems
    })
  }, [])

  const backFromFullscreen = useCallback(() => {
    setLayout(prevLayout.current!)
    prevLayout.current = null
  }, [])

  const onChangeLayout = useCallback(
    (newLayout: Layout) => {
      prevLayout.current = layout
      setLayout(newLayout)
    },
    [layout]
  )

  // Listening switch event from AiModelTabs
  useEffect(() => {
    const subscription = globalSubject.subscribe(
      SubjectTopic.SwitchFunc,
      ({ item }: { item: DraggableItem }) => {
        switchTargetItem(item, focusedIdx)
      }
    )

    return () => subscription.unsubscribe()
  }, [focusedIdx, switchTargetItem])

  useEffect(() => {
    const currentItem = activeItems[focusedIdx]

    function findTargetItemIdx(target: 'next' | 'prev'): number {
      // Find current item index
      const itemIdx = allItems.findIndex(({ id }) => id === currentItem.id)

      const targetIdx = itemIdx + (target === 'next' ? 1 : -1)
      // Target item does not exist
      if (targetIdx < 0 || targetIdx === allItems.length) {
        return -1
      }

      return targetIdx
    }

    // Navigate to the next item in the same series
    const arrowDown = shortcut$([KeyCode.ArrowDown])
      .pipe(throttle(() => interval(1000)))
      .subscribe(() => {
        const nextItemIdx = findTargetItemIdx('next')
        if (
          nextItemIdx > -1 &&
          allItems[nextItemIdx].series_inst_uid === activeItems[focusedIdx].series_inst_uid
        ) {
          switchTargetItem(allItems[nextItemIdx], focusedIdx)
        }
      })

    // Navigate to the previous item in the same series
    const arrowUp = shortcut$([KeyCode.ArrowUp])
      .pipe(throttle(() => interval(1000)))
      .subscribe(() => {
        const prevItemIdx = findTargetItemIdx('prev')
        if (
          prevItemIdx > -1 &&
          allItems[prevItemIdx].series_inst_uid === activeItems[focusedIdx].series_inst_uid
        ) {
          switchTargetItem(allItems[prevItemIdx], focusedIdx)
        }
      })

    // Navigate to the first item in next series
    const arrowRight = shortcut$([KeyCode.ArrowRight])
      .pipe(throttle(() => interval(1000)))
      .subscribe(() => {
        const nextItemIdx = findTargetItemIdx('next')
        if (nextItemIdx === -1) {
          return
        }
        // Iterate to the end and find the first item in next series
        for (let idx = nextItemIdx; idx < allItems.length; idx++) {
          const item = allItems[idx]
          if (item.series_inst_uid !== currentItem.series_inst_uid) {
            switchTargetItem(item, focusedIdx)
            break
          }
        }
      })

    // Navigate to the first item in previous series
    const arrowLeft = shortcut$([KeyCode.ArrowLeft])
      .pipe(throttle(() => interval(1000)))
      .subscribe(() => {
        const prevItemIdx = findTargetItemIdx('prev')
        if (prevItemIdx === -1) {
          return
        }
        let targetSeriesInstUid: string | undefined
        // Iterate to the beginning and find the last item in previous series
        for (let idx = prevItemIdx; idx >= 0; idx--) {
          const item = allItems[idx]
          if (item.series_inst_uid !== currentItem.series_inst_uid) {
            targetSeriesInstUid = item.series_inst_uid
            break
          }
        }
        // We are the first series, there is no previous series
        if (!targetSeriesInstUid) {
          return
        }
        const targetItem = allItems.find((item) => item.series_inst_uid === targetSeriesInstUid)
        switchTargetItem(targetItem!, focusedIdx)
      })

    return () => {
      arrowDown.unsubscribe()
      arrowUp.unsubscribe()
      arrowRight.unsubscribe()
      arrowLeft.unsubscribe()
    }
  }, [activeItems, focusedIdx, allItems, switchTargetItem])

  return (
    <DndProvider backend={HTML5Backend}>
      <Grid container className={classes.patientDetail} wrap="nowrap">
        <Grid item className={classes.sidebar}>
          <StudyList
            studiesByYear={studiesByYear}
            onOpenAi={() => onSwitchPlugin('ai')}
            activeItems={activeItems}
          />
        </Grid>
        <Grid item={true} className={classes.main}>
          {activeItems.length > 0 && (
            <>
              <Toolbar
                layout={layout}
                changeLayout={onChangeLayout}
                currentItem={activeItems[focusedIdx]}
                onSwitchPlugin={onSwitchPlugin}
                onSwitchDicomTool={onSwitchDicomTool}
                onSwitchHangingProtocol={onSwitchHangingProtocol}
              />
              <LayoutPanel
                items={activeItems}
                layout={layout}
                focusedIdx={focusedIdx}
                changeLayout={onChangeLayout}
                backFromFullscreen={backFromFullscreen}
                focusItem={setFocusedIdx}
                switchTargetItem={switchTargetItem}
              />
            </>
          )}
        </Grid>
      </Grid>
    </DndProvider>
  )
}
