import { Box, CircularProgress, Slider, Typography } from '@material-ui/core'
import { useEffect, useRef, useState } from 'react'
import { ColorResult, RGBColor, SketchPicker } from 'react-color'
import { ReactComponent as SettingIcon } from 'assets/icon/settings.svg'
import { useThreeDStyles } from 'PatientDetail/styles'

const DEFAULT_OPACITY = 0.3
const DEFAULT_THRESHOLDS = [0, 1000]
const DEFAULT_WINDOWS = [0, 1000]
const DEFAULT_COLOR = { r: 255, g: 255, b: 255 } as RGBColor

interface Props {
  filepath: string
  id: string
}

export default function ThreeDViewer(props: Props) {
  const { filepath, id } = props
  const classes = useThreeDStyles()
  const [isReady, toggleReady] = useState(true)
  const renderRef = useRef(new (window as any).X.renderer3D())
  const volumeRef = useRef(new (window as any).X.volume())
  const [config, setConfig] = useState({
    opacity: DEFAULT_OPACITY,
    color: DEFAULT_COLOR,
    thresholds: DEFAULT_THRESHOLDS,
    windows: DEFAULT_WINDOWS,
  })
  const [showPanel, togglePanel] = useState(false)

  useEffect(() => {
    renderRef.current.container = id
    renderRef.current.init()

    volumeRef.current.file = filepath
    volumeRef.current.volumeRendering = true
    volumeRef.current.opacity = DEFAULT_OPACITY

    renderRef.current.add(volumeRef.current)

    renderRef.current.camera.position = [120, 120, 120]
    renderRef.current.render()
    renderRef.current.onShowtime = () => {
      setConfig((prev) => ({
        ...prev,
        thresholds: [volumeRef.current.lowerThreshold, volumeRef.current.upperThreshold],
        windows: [volumeRef.current.windowLow, volumeRef.current.windowHigh],
      }))
      // DEMO CODE
      volumeRef.current.lowerThreshold = 50
      volumeRef.current.highThreshold = 1200
      volumeRef.current.windowHigh = 600
    }

    // renderRef.current.onRender = function () {
    //   // Rotate the camera to pre-load all the angles in case user encounter laggy dragging
    //   renderRef.current.camera.rotate([5, 5])
    // }

    const timeout = setTimeout(() => {
      // Clear the render function and reset the camera position
      // renderRef.current.onRender = function () {}
      // renderRef.current.camera.position = [120, 120, 120]
      toggleReady(true)
    }, 8000)

    return () => clearTimeout(timeout)
  }, [filepath, id])

  const onOpacityChange = (_: any, newValue: number | number[]) => {
    setConfig((prev) => ({ ...prev, opacity: newValue as number }))
    volumeRef.current.opacity = newValue
  }

  const onThresholdChange = (_: any, newValue: number | number[]) => {
    const newThresholds = newValue as number[]
    volumeRef.current.lowerThreshold = newThresholds[0]
    volumeRef.current.upperThreshold = newThresholds[1]
    setConfig((prev) => ({ ...prev })) // force re-rendering
  }

  const onWindowChange = (_: any, newValue: number | number[]) => {
    const newWindows = newValue as number[]
    volumeRef.current.windowLow = newWindows[0]
    volumeRef.current.windowHigh = newWindows[1]
    setConfig((prev) => ({ ...prev })) // force re-rendering
  }

  const onColorChange = (color: ColorResult) => {
    const { r, g, b } = color.rgb
    setConfig((prev) => ({ ...prev, color: color.rgb }))
    volumeRef.current.maxColor = [r / 255, g / 255, b / 255]
  }

  return (
    <div style={{ height: '100%', position: 'relative' }}>
      {/* XTK canvas container */}
      <div className={classes.xtkWrapper} id={id} />
      {/* Block user mouse event before the model is ready */}
      {!isReady && (
        <div className={classes.loading}>
          <CircularProgress size={40} className="progress" />
        </div>
      )}
      {/* DEMO CODE */}
      {isReady && (
        <>
          <SettingIcon className={classes.settingIcon} onClick={() => togglePanel(!showPanel)} />
          <section className={`${classes.thresholdPanel} ${showPanel ? 'active' : ''}`}>
            {showPanel && (
              <>
                <div>
                  <Typography className={classes.label} gutterBottom>
                    Opacity
                  </Typography>
                  <Box display="flex" justifyContent="space-between" alignItems="center">
                    <span className={classes.value}>0</span>
                    <Slider
                      value={config.opacity}
                      min={0.1}
                      max={1}
                      step={0.1}
                      onChange={onOpacityChange}
                      className={classes.slider}
                    />
                    <span className={classes.value}>1</span>
                  </Box>
                </div>

                <div>
                  <Typography className={classes.label} gutterBottom>
                    Threshold
                  </Typography>
                  <Box display="flex" justifyContent="space-between" alignItems="center">
                    <span className={classes.value}>
                      {volumeRef.current.lowerThreshold.toFixed(0)}
                    </span>
                    <Slider
                      defaultValue={config.thresholds}
                      min={config.thresholds[0]}
                      max={config.thresholds[1]}
                      step={10}
                      onChange={onThresholdChange}
                      className={classes.slider}
                    />
                    <span className={classes.value}>
                      {volumeRef.current.upperThreshold.toFixed(0)}
                    </span>
                  </Box>
                </div>

                <div>
                  <Typography className={classes.label} gutterBottom>
                    Window
                  </Typography>
                  <Box display="flex" justifyContent="space-between" alignItems="center">
                    <span className={classes.value}>{volumeRef.current.windowLow.toFixed(0)}</span>
                    <Slider
                      defaultValue={config.windows}
                      min={config.windows[0]}
                      max={config.windows[1]}
                      step={10}
                      onChange={onWindowChange}
                      className={classes.slider}
                    />
                    <span className={classes.value}>{volumeRef.current.windowHigh.toFixed(0)}</span>
                  </Box>
                </div>

                <SketchPicker color={config.color} onChangeComplete={onColorChange} />
              </>
            )}
          </section>
        </>
      )}
    </div>
  )
}
