import { Box } from '@material-ui/core'
import { useDrag } from 'react-dnd'
import { DraggableItem, Patient, Series, Study } from 'common/types'
import { ReactComponent as CollapseIcon } from 'assets/icon/collapse.svg'
import dayjs from 'dayjs'
import React, { useCallback, useState } from 'react'
import { useStudyListStyles } from 'PatientDetail/styles'
import { parseImageItem, parseInferenceItem } from 'common/utils'
import { useQuery } from '@apollo/client'
import { GET_PATIENT } from 'PatientDetail/gql'
import { useQueryParams } from 'common/hooks'

interface Props {
  studiesByYear: Map<string, Study[]>
  activeItems: DraggableItem[]
  onOpenAi: () => void
}

function getActiveItemsMap(activeItems: DraggableItem[]) {
  const newMap = new Map()

  activeItems.forEach((item) => {
    item && newMap.set(item.id, true)
  })

  return newMap
}

const ListItem: React.FC<{
  listItem: DraggableItem
  isActive: boolean
  onDragEnd: (previousItemId: string | undefined, newItemId: string) => void
}> = (props) => {
  const { listItem, isActive, onDragEnd } = props
  const classes = useStudyListStyles()
  const [{ isDragging }, drag] = useDrag(() => ({
    type: 'box',
    item: listItem,
    end: (item, monitor) => {
      const dropResult = monitor.getDropResult<{ previousItemId: string }>()
      if (item && dropResult) {
        onDragEnd(dropResult.previousItemId, item.id)
      }
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }))

  const opacity = isDragging ? 0.4 : 1

  return (
    <div
      ref={drag}
      className={classes.thumbnail}
      style={{
        border: isActive ? '1px solid #71C3D8' : 'none',
        opacity,
      }}
    >
      <img src={listItem.thumbnail} alt={listItem.id} />
    </div>
  )
}

const List: React.FC<{
  series: Series
  defaultVisible: boolean
  onOpenAi: () => void
  onDragEnd: (previousItemId: string | undefined, newItemId: string) => void
  activeItemMap: Map<string, boolean>
}> = ({ series, defaultVisible, onOpenAi, onDragEnd, activeItemMap }) => {
  const classes = useStudyListStyles()
  const [visible, toggleVisible] = useState(defaultVisible)
  const [showAiItems, toggleAiItems] = useState(false)

  const onClickAiIcon = useCallback(() => {
    toggleAiItems(true)
    onOpenAi()
  }, [onOpenAi])

  return (
    <div key={'series' + series.series_inst_uid} className={classes.series}>
      <Box
        component="span"
        display="flex"
        justifyContent="space-between"
        alignItems="center"
        className="date"
      >
        {dayjs(series.created_at).format('MM/DD')}
        <Box display="flex" alignItems="center">
          {series.inference.length > 0 && (
            <span className="ai-icon" onClick={onClickAiIcon}>
              AI
            </span>
          )}
          <CollapseIcon
            className="arrow-icon"
            style={{ transform: visible ? 'rotate(180deg)' : 'none' }}
            onClick={() => toggleVisible(!visible)}
          />
        </Box>
      </Box>
      {visible &&
        series.images.map((img) => {
          const listItem = parseImageItem(img, series.series_inst_uid)
          return (
            <ListItem
              key={listItem.id}
              listItem={listItem}
              isActive={activeItemMap.has(listItem.id)}
              onDragEnd={onDragEnd}
            />
          )
        })}
      {visible &&
        showAiItems &&
        // 1. We show ai items only when user clicks the ai icon
        // 2. We do not support flesh removal dragging, so we
        series.inference
          .filter(({ func }) => func !== 'fleshRemoval')
          .map((inf) => {
            const listItem = parseInferenceItem(inf, series.series_inst_uid)
            return (
              <ListItem
                key={listItem.id}
                listItem={listItem}
                isActive={activeItemMap.has(listItem.id)}
                onDragEnd={onDragEnd}
              />
            )
          })}
    </div>
  )
}

export default function StudyList(props: Props) {
  const { studiesByYear, activeItems, onOpenAi } = props
  const patientId = useQueryParams('patientId')
  const { error, data } = useQuery<{ patient: Patient }, { id: string }>(GET_PATIENT, {
    variables: { id: patientId },
  })
  const classes = useStudyListStyles()
  const [activeItemMap, setActiveItemMap] = useState<Map<string, boolean>>(
    getActiveItemsMap(activeItems)
  )

  const onDragEnd = useCallback((previousItemId: string | undefined, newItemId: string) => {
    setActiveItemMap((prev) => {
      const newMap = new Map(prev)
      // it is possible that user drags to an empty block
      if (previousItemId) {
        newMap.delete(previousItemId)
      }
      newMap.set(newItemId, true)
      return newMap
    })
  }, [])

  if (error) {
    return <>'error'</>
  }

  if (!data) {
    return <>'Loading'</>
  }

  return (
    <section className={classes.studyList}>
      <div className={classes.patientName}>
        {data.patient.view_no.toString().padStart(3, '0')} {data.patient.name}
      </div>
      {[...studiesByYear.entries()].map(([year, studyGroup]) => (
        <Box key={year} mb="20px">
          <span className={classes.year}>{year}</span>
          {studyGroup.map((study) => (
            <div key={'study' + study.study_inst_uid}>
              {study.series.map((series) => {
                const isVisible =
                  activeItems.findIndex(
                    ({ series_inst_uid }) => series_inst_uid === series.series_inst_uid
                  ) > -1
                return (
                  <List
                    key={series.series_inst_uid}
                    series={series}
                    activeItemMap={activeItemMap}
                    onOpenAi={onOpenAi}
                    onDragEnd={onDragEnd}
                    defaultVisible={isVisible}
                  />
                )
              })}
              {/* End of study */}
            </div>
          ))}
          {/* End of year group */}
        </Box>
      ))}
    </section>
  )
}
