import { gql } from '@apollo/client'

export const GET_PATIENT = gql`
  query GetPatient($id: String!) {
    patient(id: $id) {
      his_patient_id
      name
      gender
      view_no
    }
  }
`

export const GET_STUDIES = gql`
  query GetStudies($ids: [ID!]!) {
    studies(ids: $ids) {
      study_id
      study_inst_uid
      patient_id
      created_at
      series {
        study_inst_uid
        series_inst_uid
        body_part_exam
        series_num
        created_at
        images {
          sop_inst_uid
          file
          thumbnail
          func
          plugin {
            result
          }
        }
        inference {
          id
          result
          func
        }
        predictions {
          file
          laterality
          view_position
        }
      }
    }
  }
`
