import { useCallback, useEffect, useRef, useState } from 'react'

import * as cornerstone from 'cornerstone-core'
import * as cornerstoneTools from 'cornerstone-tools'
import { FILES_URL, globalSubject } from 'common/utils'
import { SubjectTopic } from 'common/utils'
import { fromEvent, interval, Subscription } from 'rxjs'
import { mergeMap, map, takeUntil, tap, throttle } from 'rxjs/operators'
import { KeyCode, shortcut$ } from 'common/shortcut'
import ThreeDViewer from './ThreeDViewer'

const MAX_GAMMA_CORRECTION = 1
const MIN_GAMMA_CORRECTION = 0.5

const FleshRemoval: React.FC<{ data: { original: string; fleshRemoval: string } }> = ({ data }) => {
  const [opacity, setOpacity] = useState(1)
  const divRef = useRef<HTMLDivElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const prevOpacityRef = useRef<number>(opacity)
  const currentGammaCorrectionRef = useRef<number>(1)

  const calculateGammaCorrection = useCallback((gammaCorrection: number, delta: number) => {
    const newGamma = gammaCorrection + delta
    return newGamma > MAX_GAMMA_CORRECTION
      ? MAX_GAMMA_CORRECTION
      : newGamma < MIN_GAMMA_CORRECTION
      ? MIN_GAMMA_CORRECTION
      : newGamma
  }, [])

  // Subscribe mouse event to simulate flesh removal when user drag vertically
  useEffect(() => {
    if (!divRef.current) return

    const mouseDowns$ = fromEvent<MouseEvent>(divRef.current, 'mousedown')
    const mouseUps$ = fromEvent<MouseEvent>(divRef.current, 'mouseup')
    const mouseMoves$ = fromEvent<MouseEvent>(divRef.current, 'mousemove')

    const subscription = mouseDowns$
      .pipe(
        mergeMap((mouseDown) =>
          mouseMoves$.pipe(
            throttle(() => interval(100)),
            map((mouseMove) => mouseMove.clientY - mouseDown.clientY),
            takeUntil(
              mouseUps$.pipe(
                tap(() =>
                  setOpacity((prev) => {
                    // When mouse up, we update the previous opacity in setOpacity to avoid using "opacity"
                    // in the dependency array which will effect the subscription
                    prevOpacityRef.current = prev
                    return prev
                  })
                )
              )
            )
          )
        )
      )
      .subscribe((delta) => {
        const nextOpacity = prevOpacityRef.current + delta * 0.01
        setOpacity(nextOpacity <= 0 ? 0 : nextOpacity >= 1 ? 1 : nextOpacity)
      })

    return () => subscription.unsubscribe()
  }, [calculateGammaCorrection])

  // Subscribe mouse event to calculate gamma correction when user drag horizontally
  useEffect(() => {
    if (!divRef.current) return

    const mouseDowns$ = fromEvent<MouseEvent>(divRef.current, 'mousedown')
    const mouseUps$ = fromEvent<MouseEvent>(divRef.current, 'mouseup')
    const mouseMoves$ = fromEvent<MouseEvent>(divRef.current, 'mousemove')

    let subscription: Subscription

    const img = new Image()
    img.crossOrigin = 'anonymous'
    img.src = `${FILES_URL}/${data.fleshRemoval}`

    const resizeListener = () => {
      canvasRef.current!.width = divRef.current!.clientWidth
      canvasRef.current!.height = divRef.current!.clientHeight
      const ctx = canvasRef.current!.getContext('2d')!
      ctx.drawImage(img, 0, 0, divRef.current!.clientWidth, divRef.current!.clientHeight)
    }

    img.onload = () => {
      // IMPORTANT: we should always refer to divRef.current.clientWidth/Height
      // instead of making a local variable like
      // const width = divRef.current!.clientWidth
      // because this onload function will never update once the image is loaded
      // thus the local variable width/height will be incorrect after window resize
      window.addEventListener('resize', resizeListener)
      canvasRef.current!.width = divRef.current!.clientWidth
      canvasRef.current!.height = divRef.current!.clientHeight
      const ctx = canvasRef.current!.getContext('2d')!
      ctx.drawImage(img, 0, 0, divRef.current!.clientWidth, divRef.current!.clientHeight)

      subscription = mouseDowns$
        .pipe(
          mergeMap((mouseDown) =>
            mouseMoves$.pipe(
              throttle(() => interval(100)),
              map((mouseMove) => mouseMove.clientX - mouseDown.clientX),
              takeUntil(
                mouseUps$.pipe(
                  tap((mouseUpEvent) => {
                    // When mouse up, update the current gamma correction
                    const delta = mouseUpEvent.clientX - mouseDown.clientX
                    currentGammaCorrectionRef.current = calculateGammaCorrection(
                      currentGammaCorrectionRef.current,
                      delta / divRef.current!.clientWidth
                    )
                  })
                )
              )
            )
          )
        )
        .subscribe((delta) => {
          const gammaCorrection = calculateGammaCorrection(
            currentGammaCorrectionRef.current,
            delta / divRef.current!.clientWidth
          )

          // Step 1: redraw the original image
          ctx.drawImage(img, 0, 0, divRef.current!.clientWidth, divRef.current!.clientHeight)

          const imageData = ctx.getImageData(
            0,
            0,
            divRef.current!.clientWidth,
            divRef.current!.clientHeight
          )
          const data = imageData.data
          // Step 2: calculate the gamma correction
          for (let i = 0; i < data.length; i += 4) {
            data[i] = Math.pow(data[i] / 255, gammaCorrection) * 255
            data[i + 1] = Math.pow(data[i + 1] / 255, gammaCorrection) * 255
            data[i + 2] = Math.pow(data[i + 2] / 255, gammaCorrection) * 255
          }
          // Step 3: update the calculated pixel data to canvas
          ctx.putImageData(imageData, 0, 0)
        })
    }

    return () => {
      subscription.unsubscribe()
      window.removeEventListener('resize', resizeListener)
    }
  }, [calculateGammaCorrection, data.fleshRemoval])

  useEffect(() => {
    const decrease = shortcut$([KeyCode.ControlLeft, KeyCode.KeyR, KeyCode.Minus])
      .pipe()
      .subscribe(() => {
        setOpacity((prev) => {
          const nextOpacity = prev - 0.1
          return nextOpacity <= 0 ? 0 : nextOpacity >= 1 ? 1 : nextOpacity
        })
      })
    const increase = shortcut$([KeyCode.ControlLeft, KeyCode.KeyR, KeyCode.Equal])
      .pipe()
      .subscribe(() => {
        setOpacity((prev) => {
          const nextOpacity = prev + 0.1
          return nextOpacity <= 0 ? 0 : nextOpacity >= 1 ? 1 : nextOpacity
        })
      })

    return () => {
      decrease.unsubscribe()
      increase.unsubscribe()
    }
  }, [])

  return (
    <div
      ref={divRef}
      draggable={false}
      style={{ width: '100%', height: '100%', position: 'absolute', top: '0px', left: '0px' }}
    >
      <img
        draggable={false}
        src={`${FILES_URL}/${data.original}`}
        alt="bone"
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: 10,
          opacity,
        }}
      />
      <canvas ref={canvasRef} style={{ position: 'absolute', top: 0, left: 0, zIndex: 9 }} />
    </div>
  )
}

interface Props {
  id: string
  filepath: string
  plugin: Record<string, any>[]
  isFocused: boolean
  enableFleshRemoval: boolean
  enableCT: boolean
}

export default function ImageViewer(props: Props) {
  const { id, filepath, plugin, isFocused, enableFleshRemoval, enableCT } = props
  const elementRef = useRef(null)

  const fleshRemovalPluginIdx = plugin.findIndex(({ result }) => result.func === 'fleshRemoval')
  const ctPluginIdx = plugin.findIndex(({ result }) => result.func === 'ct')

  useEffect(() => {
    cornerstone.enable(elementRef.current!)
    const imageIds = [`wadouri:${filepath}`]

    const stack = {
      currentImageIdIndex: 0,
      imageIds: imageIds,
    }

    cornerstone.loadImage(imageIds[0]).then((image: any) => {
      cornerstoneTools.addStackStateManager(elementRef.current!)
      cornerstoneTools.addToolState(elementRef.current!, 'stack', stack)
      cornerstone.displayImage(elementRef.current!, image)
      globalSubject.next(SubjectTopic.InitXrayTool)
    })
  }, [filepath])

  useEffect(() => {
    if (!isFocused) return

    const deleteAll = shortcut$([KeyCode.ControlLeft, KeyCode.Delete])
      .pipe(
        tap(() => {
          const angleAnnotations = cornerstoneTools.getToolState(elementRef.current!, 'Angle')
          if (angleAnnotations) {
            angleAnnotations.data = []
            cornerstone.updateImage(elementRef.current!)
          }
          const lengthAnnotations = cornerstoneTools.getToolState(elementRef.current!, 'Length')
          if (lengthAnnotations) {
            lengthAnnotations.data = []
            cornerstone.updateImage(elementRef.current!)
          }
        })
      )
      .subscribe()

    const deleteSelected = shortcut$([KeyCode.Delete])
      .pipe(
        tap(() => {
          const angleAnnotations = cornerstoneTools.getToolState(elementRef.current!, 'Angle')
          if (angleAnnotations) {
            for (let idx = 0; idx < angleAnnotations.data.length; idx++) {
              const anno = angleAnnotations.data[idx]
              if (anno.active) {
                angleAnnotations.data.splice(idx, 1)
                break
              }
            }
            cornerstone.updateImage(elementRef.current!)
          }
          const lengthAnnotations = cornerstoneTools.getToolState(elementRef.current!, 'Length')
          if (lengthAnnotations) {
            for (let idx = 0; idx < lengthAnnotations.data.length; idx++) {
              const anno = lengthAnnotations.data[idx]
              if (anno.active) {
                lengthAnnotations.data.splice(idx, 1)
                break
              }
            }
            cornerstone.updateImage(elementRef.current!)
          }
        })
      )
      .subscribe()

    return () => {
      deleteAll.unsubscribe()
      deleteSelected.unsubscribe()
    }
  }, [isFocused])

  return (
    <div
      ref={elementRef}
      id={id}
      style={{
        maxWidth: '100%',
        height: '100%',
        backgroundColor: '#000000',
        outline: 'none',
      }}
      tabIndex={0}
      onContextMenu={(e) => {
        e.preventDefault()
      }}
    >
      {enableFleshRemoval && fleshRemovalPluginIdx !== -1 && (
        <FleshRemoval data={plugin![fleshRemovalPluginIdx].result.data} />
      )}
      {enableCT && ctPluginIdx !== -1 && (
        <div
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            zIndex: 10,
            height: '100%',
            width: '100%',
          }}
        >
          <ThreeDViewer
            id={`ct-${id}`}
            filepath={`${FILES_URL}/${plugin![ctPluginIdx].result.data.file}`}
          />
        </div>
      )}
    </div>
  )
}
